import React, { useState, useRef, useEffect } from "react";
import wrapper from "../../commons/WebComponentWrapper";
import { TreeView, TreeNode } from "carbon-components-react";
import SrsIcon from "./Icon";
import { EventAPI } from "../../commons/EventAPI";

const dispatchedEvents = ["ABOUT#clicked"];

/*
    Example: <srs-contenttree items=[{name: "Node1", label: "Node 1", children: [{name: "Node11", label: "Node 11"}]}, {name: "Node2", label: "Node 2", children: [{name: "Node21", label: "Node 21"}]}]/>
*/

const SrsContentTree = function (props, ref) {
    const treeRef = useRef(null);
    useEffect(() => {
        if (treeRef.current) {
            treeRef.current.addEventListener(EventAPI.ICON_CLICKED, (evt) => {
                console.log(evt.detail.id);
            })
        }
    }, [props]);
    return (
        <div ref={treeRef}>
            <TreeView label="Tree View">
                {/* {
                    props.items.map((item) => {
                        return (<li id="1" tabindex="-1" aria-selected="false" class="cds--tree-node cds--tree-node--with-icon cds--tree-leaf-node" role="treeitem">
                                    <div class="cds--tree-node__label" style="margin-left: -2rem; padding-left: 2rem;">
                                        <span>Artificial intelligence</span>
                                    </div>
                                </li>);
                    })
                } */}
                <TreeNode isExpanded={true} data-testid="Node 1" label="Node 1" renderIcon={() => {
                    return (<div style={{ display: "flex", flexDirection: "row", padding: "0px 10px"}}>
                            <srs-icon name="Add" size="16" style={{cursor: "pointer"}} elementid="node1-add"/>
                            <srs-icon name="TrashCan" size="16" style={{cursor: "pointer"}} elementid="node1-trashcan"/>
                        </div>)
                }}>
                    <TreeNode data-testid="Node 2" label="Node 2" />
                </TreeNode>
            </TreeView>
        </div>);
}

const styles = `
    
`
const props = [{
    name: "items",
    defaultValue: [],
    type: "object"
}]

const slots = [];

const WrappedComponent = wrapper(React.forwardRef(SrsContentTree), props);
export { WrappedComponent, styles, props, dispatchedEvents, slots };